import React, { Component } from 'react'


export default class StructureTag extends Component {

    constructor(props){
        super(props)

        this.state = {}
    }

    render() {
        return (
            <div className={"structTag " + (this.props.struct.selected ? "active" : "")} onClick={this.props.toggleStruct}>
                {this.props.struct.name.replace(".fdf", "")}
            </div>
        )
    }
}




