import React, { Component } from 'react'

import _ from "lodash"
import { v4 as uuidv4 } from 'uuid';

import { Button, Row, Card, Col, Icon, Switch, Chip} from 'react-materialize'
import Select from 'react-select'

import PythonApi from '../apis/PythonApi'

import { connect } from 'react-redux'
import { selectActiveStructs } from '../redux/reducers'
import { setActiveTab, addPlots, setActiveStructs, informLoadingPlot, informLoadedPlot } from '../redux/actions'

class PlotInitializer extends Component {

    constructor(props){
        super(props)

        this.state = {
            tabID: props.active.tab,
            animation: false,
            plotClass: undefined,
            plotOptions: [],
            visible: false
        }
        
    }

    get selectedStructs(){
        return this.props.activeStructs || []
    }

    componentDidMount(){

        this.listener = document.addEventListener("togglePlotInitializer", this.toggleVisibility)

        //Get the available plots
        PythonApi.getPlotOptions( plotOptions => this.setState({plotOptions}) )
    }

    componentWillUnmount(){
        document.removeEventListener("togglePlotInitializer", this.toggleVisibility)
    }

    toggleVisibility = (e) => {

        let visible;

        if (e != undefined & e.detail != undefined) {
            visible = e.detail.forceShow ? true : e.detail.forceHide? false : !this.state.visible
        } else {
            visible = !this.state.visible
        }

        this.setState({visible})
    }

    updateInitializingOptions = (key, value) => {
        this.setState({ [key]: value})
    }

    getNewPlot = () => {

        this.setState({isLoading: true})

        this.selectedStructs.forEach( struct => {

            let plotClasses = this.state.plotClass.length ? this.state.plotClass : [this.state.plotClass]

            plotClasses.forEach( plotClass => {
                
                let processID = uuidv4()
                this.props.informLoadingPlot({id: processID, struct: struct, plotClass: plotClass })
            
                PythonApi.getNewPlot({tabID: this.props.active.tab, struct: struct.id, ...this.state, plotClass: plotClass}, (plot, reqParams) => {

                    this.props.addPlots({[plot.id]: plot})
        
                    this.props.setActiveTab(reqParams.tabID)
                    this.setState({isLoading: false, visible: false})

                    this.props.informLoadedPlot(processID)
                    document.dispatchEvent(new Event("newPlot"))
        
                })

            })
            
            
        })

        this.props.setActiveStructs([])

    }

    render() {

        if (this.props.tabs.length == 0) return null

        let loadingMessage = this.state.isLoading ? <Row>We are getting your shiny new plot... wait a moment please :)</Row> : null

        return (
            <div className={"plotInitializer" + (this.state.visible ? "" : " hidden")}>

                <Card
                    style={{marginLeft: 20, marginRight: 20, background: "#5588ff"}}
                    actions={
                        <div style={{display: "flex", flexDirection: "row", justifyContent: "center", alignItems: "center"}}>

                            <a
                                className="btn-flat black-text initPlotBtn"
                                onClick={this.toggleVisibility}>
                                Dismiss
                                <Icon right>close</Icon>
                            </a>

                            <a 
                                className="btn-flat black-text initPlotBtn"
                                onClick={this.getNewPlot}
                                >
                                Initialize plot
                                <Icon right>done</Icon>
                            </a>

                            <a className="btn-flat black-text initPlotBtn"><Icon>refresh</Icon></a>
                        </div>     
                    }>
                    <Row>
                        <Col s={12} m={12} l={6}>
                            <Row>Structures</Row>

                            <div> {this.selectedStructs.map(struct => <div>{struct.name}</div>)} </div>

                        </Col>
                        
                        <Col s={12} m={6} l={3}>
                            <Row>Plot type</Row>
                            <Select
                                options={this.state.plotOptions}
                                placeholder="Choose the plot type..."
                                isClearable isSearchable isMulti
                                onChange={(obj) => this.updateInitializingOptions("plotClass", obj ? obj.value || obj.map(selected => selected.value) : undefined)}/>
                        </Col>
                        
                        <Col s={12} m={6} l={2}>
                            <Row>Tab</Row>
                            <Select
                                placeholder="Choose a tab..."
                                options={this.props.tabs.map( (tab) => ({label: tab.name, value: tab.id }))}
                                defaultValue={this.props.active.tab && _.find(this.props.tabs, ["id", this.props.active.tab])? {label: _.find(this.props.tabs, ["id", this.props.active.tab]).name, value: this.props.active.tab } : null}
                                isClearable isSearchable
                                onChange={(obj) => this.updateInitializingOptions("tabID", obj ? obj.value : undefined)}/>
                        </Col>

                        <Col s={12} m={6} l={1}>
                            <Row>Animation</Row>
                            <Switch
                                id="isAnimation"
                                checked={this.state.animation}
                                onChange={(e) => this.updateInitializingOptions("animation", e.target.checked) }/>
                        </Col>
                        
                    </Row>
                    {loadingMessage}
                </Card>

            </div>

            
            
        )
    }
}

const mapStateToProps = state => ({
    activeStructs: selectActiveStructs(state) ,
    tabs: state.tabs,
    active: state.active
})

const mapDispatchToProps = {
    setActiveTab,
    addPlots,
    setActiveStructs,
    informLoadingPlot,
    informLoadedPlot
}

export default connect(mapStateToProps, mapDispatchToProps)(PlotInitializer);
