import React, { Component } from 'react'

import _ from "lodash"

//--Components
import { Button, Row, Card, Col, Icon, Switch, Chip} from 'react-materialize'
import ReactTooltip from "react-tooltip"
import Select from 'react-select'
import PlotDashboard from '../components/PlotDashboard';

//--Python api 
import PythonApi from '../apis/PythonApi'

//--Redux
import { connect } from 'react-redux'
import {addPlots, addTabs, setNewStructures, setActiveTab, setActivePlot, setSessionTabs, setCurrentSession} from "../redux/actions"
import StructurePicker from '../components/StructurePicker'
import PlotInitializer from '../components/PlotInitializer'


class Plots extends Component {

    constructor(props){
        super(props)

        this.state = {
            displayPlotInitializer: false,
            plotToInitialize: {
                struct: false,
                animation: false
            },
            loadingPlots: [],
            plotOptions:[]
        }

        this.syncWithSession()

        document.addEventListener("newPlot", () => this.updateTabs())

        document.addEventListener("sessionUpdate", (e) => {
            e.detail.justUpdated.forEach( plotID => {

                console.log(plotID)
                
                this.setState({loadingPlots: [...this.state.loadingPlots, plotID]})
                PythonApi.getPlot(plotID, plot => {
                    this.props.addPlots({[plot.id]: plot})
                    this.setState({loadingPlots: this.state.loadingPlots.filter(id => id != plotID)})
                })

            })

        })
            
    }

    syncWithSession = (session) => {

        this.updateTabs()

        PythonApi.getPlotOptions( plotOptions => this.setState({plotOptions}) )
        PythonApi.getAvailableStructs( structures => this.props.setNewStructures(structures) )
        
        if (session){
            this.props.setCurrentSession(session)
        } else {
            PythonApi.getSessionSettings(session => this.props.setCurrentSession(session))
        }
        
    }

    componentDidMount(){

        // If there is no session, keep listening until there is one
        if (Object.keys(this.props.session).length == 0){
            let sessionFinder = setInterval(() => {
                this.syncWithSession()
                if( Object.keys(this.props.session).length == 0){
                    clearInterval(sessionFinder)
                }
            }, 500)
        }

        this.props.setActivePlot(undefined)
        
    }

    componentDidUpdate(){

        let activeTab = _.find(this.props.tabs, ["id", this.props.active.tab]) 
        
        if (!activeTab) return

        //Get the missing plots if there are any
        activeTab.plots.forEach(plotID => {

            if ( !this.props.plots[plotID] && this.state.loadingPlots.indexOf(plotID) == -1){

                this.setState({loadingPlots: [...this.state.loadingPlots, plotID]})
                PythonApi.getPlot(plotID, plot => {
                    this.props.addPlots({[plot.id]: plot})
                    this.setState({loadingPlots: this.state.loadingPlots.filter(id => id != plotID)})
                })
                
            }
        })

    }

    updateTabs = (tabs) => {

        if (tabs){
            this.props.setSessionTabs(tabs)

        } else {
            PythonApi.getSessionTabs( tabs => {
                this.props.setSessionTabs(tabs)
                
                let activeTab = _.find(tabs, ["id", this.props.active.tab])

                if (!activeTab) this.props.setActiveTab( tabs.length > 0 ? tabs[0].id : undefined)
            })
        }

    }

    newTab = () => {

        PythonApi.addNewTab( (tabs) => {
            this.updateTabs(tabs)
            this.props.setActiveTab(_.last(tabs).id)
        })
        

    }

    removeTab = (tabID) => {

        let iDeletedTab = _.findIndex(this.props.tabs, ["id", tabID])

        PythonApi.removeTab(tabID, (tabs) => {
            this.props.setActiveTab(tabs.length > 0 ? tabs[Math.min(tabs.length - 1, iDeletedTab)].id : undefined)
            this.updateTabs(tabs)
        })

    }

    updateTabParams = (tabID, newTabParams) => {

        PythonApi.updateTabParams(tabID, newTabParams, (tabs) => {
            this.updateTabs(tabs)
        })
    }

    updateTabName = (e) => {e.target.blur(); this.updateTabParams(e.target.id, {name: e.target.textContent})}

    getNewPlot = () => {

        this.setState({isLoading: true})

        PythonApi.getNewPlot({tabID: this.props.active.tab, ...this.state.plotToInitialize}, (plot, reqParams) => {

            this.updateTabs()
            this.props.addPlots({[plot.id]: plot})

            this.togglePlotInitializer()
            this.props.setActiveTab(reqParams.tabID)
            this.setState({isLoading: false})
        })

    }

    saveCurrentSession = () => {
        let path = prompt("Please provide the path to save the session.\n\nRoot directory:\n" + this.props.session.settings.rootDir)
        if (!path) return 

        PythonApi.saveSession({path})
        alert("Session saved")
    }

    loadSession = () => {

        let path = prompt("Please provide the path of the saved session.\n\nRoot directory:\n" + this.props.session.settings.rootDir)
        if (!path) return

        PythonApi.loadSession({path}, (session) => {
            console.log("Session loaded", session)
            this.syncWithSession(session)
        })
    }

    togglePlotInitializer = () => {

        document.dispatchEvent(new Event("togglePlotInitializer"))

    }

    updateInitializingOptions = (key, value) => {
        this.setState({
            plotToInitialize: {...this.state.plotToInitialize, [key]: value}
        })
    }

    renderNewPlotButton = (isInitializerDisplaying) => {

        let addNewPlotBut = isInitializerDisplaying ? null : (
            <Button
                data-tip="New plot"
                floating 
                onClick={this.togglePlotInitializer}
                icon={<Icon>add</Icon>} className="green" />
        )

        return <Button
                    floating
                    icon={<Icon>menu</Icon>}
                    fab={{direction: 'left', hoverEnabled: false}}
                    className={Object.keys(this.props.plots).length == 0 && !this.state.displayPlotInitializer? "red pulse" : "red"}
                    large
                >
                    <ReactTooltip multiline disable={this.props.session.settings ? !this.props.session.settings.showTooltips : false}/>
                    {addNewPlotBut}
                    <Button
                        data-tip="Save session"
                        floating
                        icon={<Icon>save</Icon>} 
                        onClick={this.saveCurrentSession}
                        className="yellow darken-1" />
                    <Button
                        data-tip="Load session"
                        floating
                        icon={<Icon>open_in_new</Icon>}
                        onClick={this.loadSession}
                        className="green" />
                    <Button floating icon={<Icon />} className="blue" />
                </Button>

    }

    render() {

        let tabs = this.props.tabs.map( ({id: tabID, name: tabName}) => {

            let active = tabID == this.props.active.tab

            return <div
                    key={tabID}
                    className={active ? "active plotTab" : "plotTab"}
                    onClick={active ? null : ()=> this.props.setActiveTab(tabID)}>
                <div
                    id={tabID}
                    contentEditable={active}
                    
                    onBlur={this.updateTabName}
                    onKeyUp={(e) => {if (e.keyCode === 13) this.updateTabName(e)}}
                    >{tabName}</div>
                {active ? <div className="removeTabBut" onClick={() => this.removeTab(tabID)}><Icon>remove_circle</Icon></div> : null}
            </div>
        })

        return (
            <div className="row">
                <div className="col s12 m12 l1">
                    <StructurePicker structures={this.props.structures}/>   
                </div>
                <div className="col s12 l11">
                    <div style={{display: "flex", flexWrap: "wrap", alignItems:"center"}}>
                        <div onClick={this.newTab} data-tip="New tab">
                            <Icon
                                className="newTabIcon small blue-text text-darken-3"
                                style={{paddingRight: 0}}>create_new_folder</Icon>
                        </div>
                        {tabs}
                    </div>
                    <PlotInitializer/>
                    <PlotDashboard/>
                </div>
                
                <Row>
                    {this.renderNewPlotButton(this.state.displayPlotInitializer)}
                </Row>
                
                <ReactTooltip multiline disable={this.props.session.settings ? !this.props.session.settings.showTooltips : false}/>
            </div>
        )
    }
}

const mapStateToProps = state => ({
    plots: state.plots,
    structures: state.structures,
    tabs: state.tabs,
    active: state.active,
    session: state.session
})

const mapDispatchToProps = dispatch => ({
    setCurrentSession: (session) => dispatch(setCurrentSession(session)),
    setActiveTab: (tabID) => dispatch(setActiveTab(tabID)),
    setSessionTabs: (tabs) => dispatch(setSessionTabs(tabs)),
    setActivePlot: (plot) => dispatch(setActivePlot(plot)),
    addPlots: (newPlots, tabsIDs) => dispatch(addPlots(newPlots, tabsIDs)),
    addTabs: (newTabs) => dispatch(addTabs(newTabs)),
    setNewStructures: (structures) => dispatch(setNewStructures(structures)),
})

export default connect(mapStateToProps, mapDispatchToProps)(Plots);